#contact{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.contact h1{
    font-size: 70px;
    color: transparent;
    background-image: linear-gradient(180deg, #ffb6ff, #b344ff);
    -webkit-background-clip: text;
    background-clip: text;
}
.contact ul a{
    font-size: 30px;
    color: #6344ff;
    padding: 10px 20px;
    cursor: pointer;
}

    .con {
        display: flex;
        justify-content: center;
        align-items: baseline;
        background-color: rgba(0, 0, 0, 0.12);
        margin: 40px 0;
        padding: 20px 40px;
        border-radius: 5px;
        box-shadow: 1px 1px 2px 1px #0000007a;
    }

.con p{
    margin-right: 20px;
    display: flex;
    font-size: 25px;
    color: transparent;
    background-image: linear-gradient(180deg, #b9b6ff, #6344ff);
    -webkit-background-clip: text;
    background-clip: text;
}
.search{
    display: flex;
    justify-content: space-between;
    /* border: 1px solid rgba(211, 211, 211, 0.181); */
    border-radius: 5px;
    text-align: center;
    padding: 8px 20px;
    width: 20vw;
    background: transparent;
}
.search input{
    border: none;
    width: 15vw;
    outline: none;
    color: white;
    background: transparent;
}
.search li{
    font-size: 20px;
    list-style-type: none;
    color: white;
    background: transparent;

}
@media screen and (max-width:525px) {
    .con{
        flex-direction: column;
        padding: 15px 22px;
    }
    .search input{
        width: fit-content;
    }
    #contact{
        height: 80vh;
    }
}